.intro{
    height: 80vh;
    margin-top: 100px;
}
.intro-top{
    font-size: 22px;
}
.intro-heading{
    font-size: 80px;
    font-weight: 700;
    text-transform: capitalize;
    letter-spacing: 3px;
}
.intro-bottom{
    font-size: 28px;
    font-weight: 700;
    margin-bottom: 0px;
}
.Typewriter {
    display: inline;
    font-size: 22px;
}
.intro-footer{
    margin-top: 30px;
    font-size: 20px;
}
.getResume{
    background-color: #f56506;
    color: #fff;
    padding: 15px 25px;
    
}
.getResume:hover{
    background-color: #fff;
    color: #f56506;
}
.getResume:hover i.fa-solid.fa-cloud-arrow-down.ml-2 {
    color: #f56506;
}
.getAbout{
    background-color: #fff;
    padding: 15px 35px;
    color: #f56506;
}
.getAbout:hover{
    color: #fff;
    background-color: #f56506;
}
.connect{
  display: inline;
  font-weight: 700;
}
.connect a {
    text-decoration: none;
    margin: 0px 20px;
}
.connect a i{
    color: #f56506;
    transition: 0.4s;
}
.connect a i:hover{
    color: #fff;
    transform: scale(1.3);
}
