.full-about{
    margin-top: 100px;
    margin-bottom: 100px;
}
.my-pic{
    height: 295px;
    width: 290px;
    transition: 1s;
    border-radius: 10px;
    filter: grayscale(80%);
    margin-right: 50px;
}
.pic{
    height: 340px;
    width: 450px;
}
.pic:hover .my-pic{
    transform: scale(1.1);
    filter: grayscale(0%) ;
}
.about-me{
   line-height: 34px;
}